    import {
        mapState,
        mapMutations
    } from 'vuex'

    export default {
        computed: {
            ...mapState(['hasLogin', 'forcedLogin'])
        },
		onLoad() {
		    if (!this.hasLogin) {
				uni.navigateTo({
				    url: '../home/home',
				});
			}
		},
		data(){
			return{
				skin: false
			}
		},
        methods: {
            ...mapMutations(['logout']),
            bindLogin() {
                uni.navigateTo({
                    url: '../home/home',
                });
            },
            bindLogout() {
                this.logout();
				console.log(this.forcedLogin)
                /**
                 * 如果需要强制登录跳转回登录页面
                 */
                if (this.forcedLogin) {
                    uni.reLaunch({
                        url: '../home/home',
                    });
                } 
            },
			SwitchSex(e) {
				this.skin = e.detail.value
			}
        }
    }
